import Vue from "vue";
import VueRouter from "vue-router";
import GIcon from "../views/icon/icon-demo.vue";
import GButton from "../views/button/button-demo.vue";
import GTabs from "../views/tabs/tabs-demo.vue";
import GInput from "../views/input/input-demo.vue";
import Grid from "../views/grid/grid-demo.vue";
import Layout from "../views/Layout/layout-demo.vue";
import Popover from "../views/popover/popover-demo.vue";
import GCollaspe from "../views/collaspe/collapse-demo.vue";
import Home from "../views/Home.vue";
Vue.use(VueRouter);

const routes = [
  { path: "/", redirect: "/home" },
  {
    path: "/home",
    name: "Home",
    component: Home,
    children: [
      {
        path: "/button",
        name: "GButton",
        component: GButton,
      },
      {
        path: "/icon",
        name: "GIcon",
        component: GIcon,
      },
      {
        path: "/tab",
        name: "GTabs",
        component: GTabs,
      },
      {
        path: "/input",
        name: "GInput",
        component: GInput,
      },
      {
        path: "/grid",
        name: "Grid",
        component: Grid,
      },
      {
        path: "/layout",
        name: "Layout",
        component: Layout,
      },
      {
        path: "/popover",
        name: "PopOver",
        component: Popover,
      },
      {
        path: "/collapse",
        name: "collapse",
        component: GCollaspe,
      },
    ],
  },

  // {
  //   path: "/about",
  //   name: "About",
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () =>
  //     import(/* webpackChunkName: "about" */ "../views/About.vue"),
  // },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
